import React, { useRef, useState } from 'react'
import { List, ListItem, ListItemIcon, IconButton, ListItemText, Button, Typography, TextField, FormControl, InputLabel, Select, MenuItem } from '@mui/material';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';
import { format } from 'date-fns';
import { v4 as uuid } from 'uuid';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { fi } from 'date-fns/locale';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';

interface Props {
    tehtavat : Tehtava[];
    setTehtavat : (arg0 : Tehtava[]) => void;
}

const UusiPaiva : React.FC<Props> = ({tehtavat, setTehtavat}) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();

    const [pvm, setPvm] = useState<Date>(new Date());

    const uusiTehtavaRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef2 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef3 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef4 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef5 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

    const PaivanLisays = (): void => {
      const paivannimiValue = uusiTehtavaRef.current?.value || 'Uusi piv';
  
      const existingTehtavaIndex = tehtavat.findIndex(tehtava => tehtava.paivannimi === paivannimiValue);
  
      if (existingTehtavaIndex !== -1) {

          const updatedTehtavat = [...tehtavat];
          updatedTehtavat[existingTehtavaIndex].liikkeet.push({
              id: uuid(),

              liike: uusiTehtavaRef2.current?.value || '',
              sarjat: uusiTehtavaRef3.current?.value || '',
              toistot: uusiTehtavaRef4.current?.value || '',
              paino : uusiTehtavaRef5.current?.value || '',
          });
          setTehtavat(updatedTehtavat);
      } else {

          const uusiTehtava: Tehtava = {
              id: uuid(),
              paivannimi: paivannimiValue,
              liikkeet: [
                  {
                      id: uuid(),
                      liike: uusiTehtavaRef2.current?.value || '',
                      sarjat: uusiTehtavaRef3.current?.value || '',
                      toistot: uusiTehtavaRef4.current?.value || '',
                      paino : uusiTehtavaRef5.current?.value || '',
                  },
              ],
              sarjat: "",
              toistot: "",
              paino : "",
              paivamaara : pvm,
          };
          setTehtavat([...tehtavat, uusiTehtava]);
      }
  
      navigate('/');
  };
  return(
        <>  
<LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
    <Typography variant='h4'>Tll voit list uuden pivn treeniohjelmaasi varten. Lis samalla yksi liike.</Typography>

<TextField 
    label="Anna pivlle nimi"
    inputRef={uusiTehtavaRef}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Liike"
    inputRef={uusiTehtavaRef2}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Paino"
    inputRef={uusiTehtavaRef5}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Sarjat"
    inputRef={uusiTehtavaRef3}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Toistot"
    inputRef={uusiTehtavaRef4}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>

<Button
    variant="contained"
    fullWidth={true}
    onClick={PaivanLisays}
>Tallenna</Button>  

<Button
    fullWidth={true}
    component={Link}
    to="/"
>Peruuta</Button>    
</LocalizationProvider>



        </>
)
}

export default UusiPaiva;