import React, { useRef, useState } from 'react'
import { List, ListItem, ListItemIcon, IconButton, ListItemText, Button, Typography, TextField } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { format } from 'date-fns';
import { v4 as uuid } from 'uuid';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { fi } from 'date-fns/locale';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';

interface Props {
    tehtavat : Tehtava[];
    setTehtavat : (arg0 : Tehtava[]) => void;
}

const Muokkaa : React.FC<Props> = ({tehtavat, setTehtavat}) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();

    const { id } = useParams<any>();

    const uusiTehtavaRef : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef2 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef3 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef4 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

    const nimenvaihto = (): void => {
        const index = tehtavat.findIndex((tehtava: Tehtava) => tehtava.id === id);
    
        if (index !== -1) {
            const uusinimi = [...tehtavat];
            const liikkeetKopio = [...uusinimi[index].liikkeet];
    
            uusinimi[index] = {
                ...uusinimi[index],
                paivannimi: uusiTehtavaRef.current?.value || 'Uusi piv',
                liikkeet: liikkeetKopio, 
                sarjat: uusiTehtavaRef3.current?.value || "",
                toistot: uusiTehtavaRef4.current?.value || "",
            }
    
            setTehtavat(uusinimi);
        }
        navigate("/");
    };

    return (
        <>  
    <Typography variant='h4'>Vaihda pivn nimi</Typography>


<TextField 
    label="paivannimi"
    inputRef={uusiTehtavaRef}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<Button
    variant="contained"
    fullWidth={true}
    onClick={nimenvaihto}
>Tallenna</Button>  

<Button
    fullWidth={true}
    component={Link}
    to="/"
>Peruuta</Button>    



        </>
    )
}

export default Muokkaa;