import React, { useRef, useState } from 'react'
import { List, ListItem, ListItemIcon, IconButton, ListItemText, Button, Typography, TextField, MenuItem, Select, FormControl, InputLabel } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { format } from 'date-fns';
import { v4 as uuid } from 'uuid';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { fi } from 'date-fns/locale';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';


interface Props {
    tehtavat : Tehtava[];
    setTehtavat : (arg0 : Tehtava[]) => void;
}


const Tarkastele : React.FC<Props> = ({tehtavat, setTehtavat}) : React.ReactElement => {

    const navigate : NavigateFunction = useNavigate();
    const { id } = useParams<any>();


    const uusiTehtavaRef2 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef3 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef4 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const uusiTehtavaRef5 : React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

    const lisaaliike = (): void => {


        const updatedTehtavat = tehtavat.map((tehtava) => {
            if (tehtava.id === id) {
                return {
                    ...tehtava,
                    liikkeet: [
                        ...tehtava.liikkeet,
                        {
                            id: uuid(),
                            liike: uusiTehtavaRef2.current?.value || '',
                            sarjat: uusiTehtavaRef3.current?.value || '',
                            toistot: uusiTehtavaRef4.current?.value || '',
                            paino : uusiTehtavaRef5.current?.value || '',
                        },
                    ],
                };
            }
            return tehtava;
        });

        setTehtavat(updatedTehtavat);
        navigate('/');
    };
    

    return (
        <>  

    <Typography variant='h4'>Lis uusi liike</Typography>


    <TextField 
    label="Liike"
    inputRef={uusiTehtavaRef2}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Paino"
    inputRef={uusiTehtavaRef5}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Sarjat"
    inputRef={uusiTehtavaRef3}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<TextField 
    label="Toistot"
    inputRef={uusiTehtavaRef4}
    variant="outlined"
    fullWidth={true}
    sx={{marginBottom : "10px"}}
/>
<Button
    variant="contained"
    fullWidth={true}
    onClick={lisaaliike}
>Tallenna</Button>  

<Button
    fullWidth={true}
    component={Link}
    to="/"
>Peruuta</Button>    




        </>
    )
}

export default Tarkastele;