import React, { useEffect } from 'react';
import { Button, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';

interface Props {
  tehtavat: Tehtava[];
  setTehtavat: (arg0: Tehtava[]) => void;
}

const PoistaLiike: React.FC<Props> = ({ tehtavat, setTehtavat }): React.ReactElement => {
  const navigate: NavigateFunction = useNavigate();
  const { tehtavaId, liikeId } = useParams<any>();


  useEffect(() => {
  const poistettavaTehtavaIndex = tehtavat.findIndex((tehtava: Tehtava) => tehtava.id === tehtavaId);

  if (poistettavaTehtavaIndex !== -1) {
    const updatedTehtavat = [...tehtavat];
    const poistettavaLiikeIndex = updatedTehtavat[poistettavaTehtavaIndex].liikkeet.findIndex(
      (liike: Liike) => liike.id === liikeId
    );

    if (poistettavaLiikeIndex !== -1) {
      updatedTehtavat[poistettavaTehtavaIndex].liikkeet.splice(poistettavaLiikeIndex, 1);
      setTehtavat(updatedTehtavat);
    }
  }

  navigate(`/`);
}, [])
  return (
    <>
      <Typography sx={{ marginBottom: "20px;" }}>
        Liike on poistettu onnistuneesti.
      </Typography>

      <Button variant="contained" fullWidth={true} component={Link} to={`/`}>
        Takaisin
      </Button>
    </>
  );
};

export default PoistaLiike;
