import React, { useEffect, useRef, useState } from 'react';
import { Route, Routes } from 'react-router';
import Aloitus from './Komponentit/Aloitus'
import UusiPaiva from './Komponentit/UusiPaiva';
import Muokkaa from './Komponentit/Muokkaa';
import Poista from './Komponentit/Poista';
import Tarkastele from './Komponentit/Tarkastele';
import PoistaLiike from './Komponentit/PoistaLiike';




const App : React.FC = () : React.ReactElement => {

  const [tehtavat, setTehtavat] = useState<Tehtava[]>([]);

  const kaynnistetty : React.MutableRefObject<boolean> = useRef(false);

  useEffect(() => {

    if (!kaynnistetty.current)  {

      if (localStorage.getItem("tehtavalista")) {

        setTehtavat(JSON.parse(String(localStorage.getItem("tehtavalista"))).map((tehtava : Tehtava) => {
          return {
            ...tehtava,
          paivamaara : new Date(tehtava.paivamaara)
          }
        }))
      }

    }

    return () => {
      kaynnistetty.current = true;
    }

  }, []);

  useEffect(() => {

    localStorage.setItem("tehtavalista", JSON.stringify(tehtavat));

  }, [tehtavat]);

  return (
<>

<Routes>

  <Route path="/" element={<Aloitus tehtavat={tehtavat} setTehtavat={setTehtavat}/>} />
  <Route path="/UusiPaiva" element={<UusiPaiva tehtavat={tehtavat} setTehtavat={setTehtavat}/>} />
  <Route path="/Muokkaa/:id" element={<Muokkaa tehtavat={tehtavat} setTehtavat={setTehtavat}/>} />
  <Route path="/Poista/:id" element={<Poista tehtavat={tehtavat} setTehtavat={setTehtavat}/>} />
  <Route path="/Tarkastele/:id" element={<Tarkastele tehtavat={tehtavat} setTehtavat={setTehtavat}/>} />
  <Route path="/PoistaLiike/:tehtavaId/:liikeId" element={<PoistaLiike tehtavat={tehtavat} setTehtavat={setTehtavat} />} />

  
</Routes>

</>
  );
}

export default App;
