import React from 'react'
import { List, ListItem, ListItemIcon, IconButton, ListItemText, Button } from '@mui/material';
import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import DeleteIcon from '@mui/icons-material/Delete';

interface Props {
    tehtavat : Tehtava[];
    setTehtavat : (arg0 : Tehtava[]) => void;
}

const Aloitus : React.FC<Props> = ({tehtavat, setTehtavat}) : React.ReactElement => {
    const oikeeJarjestys = [...tehtavat].sort((a, b) => b.paivamaara - a.paivamaara);

    return (
        <>  

           <Button 
           variant='contained'
           fullWidth={true}
           component={Link}
           to="UusiPaiva">
            Lis uusi piv</Button>



<List>
{oikeeJarjestys.map( (tehtava : Tehtava, idx : number) => {


return (
            <ListItem key={idx}>

<ListItemIcon>
    <IconButton 
        component={Link}
        to={`/Poista/${tehtava.id}`}
        edge="end"
    >
        <DeleteIcon />
    </IconButton>
    </ListItemIcon>
    
    <ListItemText 
  primary={`${tehtava.paivannimi} ${tehtava.paivamaara ? format(tehtava.paivamaara, "d.M.Y HH:mm") : ''}`}
  secondary={<Link to={`/Muokkaa/${tehtava.id}`}>Vaihda nimi</Link>}
  
/>
<ListItemIcon>
    <IconButton 
        component={Link}
        to={`/Tarkastele/${tehtava.id}`}
        edge="end"
    >Lis liike   
    </IconButton>
    </ListItemIcon>
    <List>
{tehtava.liikkeet.map((liike: Liike, liikeIdx : number) => (


        <ListItem key={liikeIdx}>

<ListItemIcon>
    <IconButton 
        component={Link}
        to={`/PoistaLiike/${tehtava.id}/${liike.id}`}
        edge="end"
    >
        <DeleteIcon />
    </IconButton>
    </ListItemIcon>

<ListItemText
                        primary={liike.liike}
                        secondary={`Paino: ${liike.paino} Sarjoja: ${liike.sarjat} Toistoja: ${liike.toistot}`}
                    />
        </ListItem>
))}
      </List>

</ListItem>
);

  
})}
</List>

            

        </>
    )
}

export default Aloitus;